.full-width-control-wrapper {
	grid-column: 1 / -1;
}

.custom-block {
    border: 5px solid #0071BC;
    background-color: #CCE3F2;
    border-radius: 5px;
    padding: 1rem;
}

.video-container-block, .autoplay-video-container-block {
    border-color: #E24C74;
    background-color: #F9DBE3;
}

.content-container-block, .accordion-block {
    border-color: #037956;
    background-color: #CDE4DD;
}

.review-carousel-container-block, .quad-image-button-block {
    border-color: #EF5229;
    background-color: #F9BAA9;
}

.review-container-block {
    border-color: #EF7C35;
    background-color: #EFAA7F;
}

.custom-label {
    text-transform: uppercase;
    font-size: 11px;
    font-weight: 500;
    line-height: 2.4;
}

.flex-container {
    display: flex;
    -webkit-box-align: center;
    align-items: center;
    flex-flow: wrap;
    gap: calc(16px);
    -webkit-box-pack: justify;
    justify-content: space-between;
    width: 100%;
    margin-bottom: 1rem;

    .flex-item {
        flex-grow: 1;
        justify-content: center;
    }
}